/**
 * MaturitySegmentDisplay Component
 *
 * 2D heatmap visualization showing operational vs strategic maturity positioning.
 * Each cell represents a specific score combination colored by maturity tier.
 */

import { useState } from 'react';
import { User, Building2, Settings, Target, Bot } from 'lucide-react';

interface MaturitySegmentDisplayProps {
  operationalScore: number;
  strategicScore: number;
}

// Maturity ranking logic with adjusted thresholds for proper band alignment
export function getMaturityRanking(opScore: number, stratScore: number): string {
  if (opScore >= 24 && stratScore >= 22) return 'Autonomous';
  if (opScore >= 19 && stratScore >= 18) return 'Controlled';
  if (opScore >= 14 && stratScore >= 13) return 'Managed';
  if (opScore >= 9 && stratScore >= 9) return 'Siloed';
  return 'Manual';
}

export default function MaturitySegmentDisplay({
  operationalScore,
  strategicScore
}: MaturitySegmentDisplayProps) {
  const currentTier = getMaturityRanking(operationalScore, strategicScore);
  const [showTooltip, setShowTooltip] = useState(false);

  // Create 2D heatmap matrix - strategic Y-axis (25 down to 6), operational X-axis (6 to 30)
  const createHeatmapGrid = () => {
    const grid = [];

    // Strategic maturity scale (Y-axis, 25 down to 6)
    for (let stratY = 25; stratY >= 6; stratY--) {
      const row = [];

      // Operational maturity scale (X-axis, 6 to 30)
      for (let opX = 6; opX <= 30; opX++) {
        const tier = getMaturityRanking(opX, stratY);
        const isCurrentPosition = opX === Math.round(operationalScore) && stratY === Math.round(strategicScore);

        // Solid colors per tier matching original
        let bgColor = '';
        switch (tier) {
          case 'Manual': bgColor = 'bg-[#06303b]'; break;
          case 'Siloed': bgColor = 'bg-[#02515e]'; break;
          case 'Managed': bgColor = 'bg-[#0d828d]'; break;
          case 'Controlled': bgColor = 'bg-[#27c4c5]'; break;
          case 'Autonomous': bgColor = 'bg-[#0cf4e9]'; break;
        }

        row.push({
          opX,
          stratY,
          tier,
          bgColor,
          isCurrentPosition
        });
      }
      grid.push(row);
    }
    return grid;
  };

  const heatmapGrid = createHeatmapGrid();

  // Tier definitions
  const tiers = [
    { name: 'Manual', icon: User, description: 'Basic processes with limited automation' },
    { name: 'Siloed', icon: Building2, description: 'Departmental tools with minimal integration' },
    { name: 'Managed', icon: Settings, description: 'Coordinated processes with selective automation' },
    { name: 'Controlled', icon: Target, description: 'Orchestrated workflow with real-time visibility' },
    { name: 'Autonomous', icon: Bot, description: 'Fully automated with predictive capabilities' }
  ];

  return (
    <div className="flex flex-col lg:flex-row gap-6">
        {/* Left side - Tier descriptions */}
        <div className="lg:w-1/3 space-y-4 pr-4">
          <h3 className="text-xl font-semibold mb-8 text-white">
            Your maturity ranking
          </h3>

          <div className="space-y-4">
            {tiers.map((tier) => {
              const IconComponent = tier.icon;
              const isSelected = tier.name === currentTier;

              return (
                <div
                  key={tier.name}
                  className={`flex items-center gap-3 p-4 rounded-xl transition-all duration-300 ${
                    isSelected
                      ? 'bg-teal-900/40 border border-teal-600 shadow-lg ring-2 ring-teal-700'
                      : 'hover:bg-slate-700/30'
                  }`}
                >
                  <IconComponent className={`w-6 h-6 ${isSelected ? 'text-teal-400' : 'text-slate-500'}`} />
                  <div>
                    <div className={`font-semibold ${isSelected ? 'text-teal-400 font-bold' : 'text-slate-300'}`}>
                      {tier.name}
                    </div>
                    <div className={`text-sm ${isSelected ? 'text-teal-400/80' : 'text-slate-500'}`}>
                      {tier.description}
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>

        {/* Right side - Heatmap matrix */}
        <div className="lg:w-2/3">
          <div className="relative">
            {/* Y-axis label (rotated) */}
            <div className="absolute -left-16 top-1/2 -translate-y-1/2 -rotate-90 text-sm font-medium text-slate-400 whitespace-nowrap">
              Strategic maturity
            </div>

            {/* Y-axis numbers */}
            <div className="absolute -left-8 text-xs text-slate-500">
              <div className="absolute leading-none font-bold text-slate-300" style={{ top: '12px' }}>25</div>
              <div className="absolute leading-none" style={{ top: `${(25-22) * 32 + 12}px` }}>22</div>
              <div className="absolute leading-none" style={{ top: `${(25-18) * 32 + 12}px` }}>18</div>
              <div className="absolute leading-none" style={{ top: `${(25-14) * 32 + 12}px` }}>14</div>
              <div className="absolute leading-none font-bold text-slate-300" style={{ top: `${(25-10) * 32 + 12}px` }}>10</div>
              <div className="absolute leading-none" style={{ top: `${(25-6) * 32 + 12}px` }}>6</div>
            </div>

            {/* Zone labels at top */}
            <div className="flex justify-between text-xs font-semibold text-white mb-1 ml-1 pr-1" style={{ marginLeft: '0' }}>
              <span className="flex-1 text-center">MANUAL</span>
              <span className="flex-1 text-center">SILOED</span>
              <span className="flex-1 text-center">MANAGED</span>
              <span className="flex-1 text-center">CONTROLLED</span>
              <span className="flex-1 text-center">AUTONOMOUS</span>
            </div>

            {/* Main heatmap grid */}
            <div className="border border-slate-500 rounded-2xl overflow-hidden shadow-lg">
              {heatmapGrid.map((row, rowIndex) => (
                <div key={rowIndex} className="flex">
                  {row.map((cell) => (
                    <div
                      key={`${cell.opX}-${cell.stratY}`}
                      className={`w-10 h-8 relative border border-white/10 ${cell.bgColor} hover:opacity-80 cursor-pointer transition-all duration-200`}
                      title={`Op: ${cell.opX}, Strat: ${cell.stratY} - ${cell.tier}`}
                      onMouseEnter={() => {
                        if (cell.isCurrentPosition) setShowTooltip(true);
                      }}
                      onMouseLeave={() => setShowTooltip(false)}
                    >
                      {cell.isCurrentPosition && (
                        <div className="absolute inset-0 flex items-center justify-center z-10">
                          {/* Position marker with animation */}
                          <div className="relative flex h-4 w-4">
                            <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-teal-400 opacity-75"></span>
                            <span className="relative inline-flex rounded-full h-4 w-4 bg-white border-2 border-teal-500 shadow-lg"></span>
                          </div>

                          {/* Tooltip */}
                          {showTooltip && (
                            <div className="absolute -top-16 left-1/2 transform -translate-x-1/2 bg-slate-800 text-white text-xs px-3 py-2 rounded-lg shadow-lg z-20 whitespace-nowrap border border-slate-600">
                              <div className="font-medium">Operational: {operationalScore}/30</div>
                              <div className="font-medium">Strategic: {strategicScore}/25</div>
                              <div className="font-bold text-teal-300">Tier: {currentTier}</div>
                              <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-slate-800"></div>
                            </div>
                          )}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              ))}
            </div>

            {/* X-axis numbers */}
            <div className="flex justify-between text-xs text-slate-500 mt-2 px-1">
              {[6, 10, 14, 18, 22, 26, 30].map(num => (
                <div key={num} className={num % 10 === 0 ? 'font-bold text-slate-300' : ''}>
                  {num}
                </div>
              ))}
            </div>

            {/* X-axis label */}
            <div className="text-center text-sm font-medium text-slate-400 mt-2">
              Operational maturity
            </div>

            {/* Current position indicator */}
            <div className="mt-4 p-3 bg-slate-900/50 rounded-lg border border-slate-600">
              <div className="text-center">
                <div className="text-sm text-slate-400 mb-1">Your position:</div>
                <div className="font-bold text-lg text-white">
                  {currentTier} ({operationalScore}/30, {strategicScore}/25)
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
  );
}
