import jsPDF from 'jspdf';

import robotoRegularTtfUrl from '@/assets/fonts/Roboto-Regular.ttf?url';
import robotoBoldTtfUrl from '@/assets/fonts/Roboto-Bold.ttf?url';
import robotoItalicTtfUrl from '@/assets/fonts/Roboto-Italic.ttf?url';
import type { MaturityResults } from '@shared/types';
import type { MaturitySegment, RecommendedAction } from '@/data/referenceData';
import type { MaturityQuestion } from '@/constants/maturityQuestions';
import { getBandDescription } from '@/utils/maturityBanding';

type PdfInput = {
  answers: number[];
  maturityResults: MaturityResults;
  segmentData: MaturitySegment | null;
  actionsData: RecommendedAction[];
  questions: MaturityQuestion[];
  clientName: string;
};

const COLOR = {
  pageBg: [255, 255, 255] as const,
  heading: [0, 97, 112] as const, // #006170
  body: [8, 32, 41] as const, // #082029
  midGray: [75, 85, 99] as const, // #4B5563
  footer: [115, 115, 115] as const, // #737373
  grid: [209, 209, 209] as const, // #D1D1D1
  divider: [26, 200, 198] as const, // #1AC8C6
  cardBg: [242, 242, 242] as const, // #F2F2F2
  markerRed: [230, 51, 51] as const, // #E63333
};

const MATRIX = {
  minOp: 6,
  maxOp: 30,
  minStrat: 5,
  maxStrat: 25,
};

const FONT = {
  family: 'Roboto',
  regularFile: 'Roboto-Regular.ttf',
  boldFile: 'Roboto-Bold.ttf',
  italicFile: 'Roboto-Italic.ttf',
};

const fontBase64Cache = new Map<string, string>();
let robotoRegistered = false;

const clamp = (value: number, min: number, max: number) => Math.min(max, Math.max(min, value));

const getSelectedOptionText = (questions: MaturityQuestion[], questionIndex: number, score: number): string => {
  if (questionIndex >= questions.length || score < 1 || score > 5) {
    return 'No response';
  }
  return questions[questionIndex].options[score - 1] || 'No response';
};

const getMatrixTier = (operationalScore: number, strategicScore: number): string => {
  if (operationalScore >= 24 && strategicScore >= 22) return 'Autonomous';
  if (operationalScore >= 19 && strategicScore >= 18) return 'Controlled';
  if (operationalScore >= 14 && strategicScore >= 13) return 'Managed';
  if (operationalScore >= 9 && strategicScore >= 9) return 'Siloed';
  return 'Manual';
};

const getTierFill = (tier: string) => {
  switch (tier) {
    case 'Manual':
      return [217, 235, 237] as const; // #D9EBED
    case 'Siloed':
      return [184, 219, 224] as const; // #B8DBE0
    case 'Managed':
      return [140, 199, 204] as const; // #8CC7CC
    case 'Controlled':
      return [89, 179, 179] as const; // #59B3B3
    case 'Autonomous':
      return [38, 153, 148] as const; // #269994
    default:
      return [184, 219, 224] as const;
  }
};

const splitParagraph = (pdf: jsPDF, text: string, width: number) => pdf.splitTextToSize(text, width);

const paintPageBackground = (pdf: jsPDF) => {
  const pageWidth = pdf.internal.pageSize.getWidth();
  const pageHeight = pdf.internal.pageSize.getHeight();
  pdf.setFillColor(...COLOR.pageBg);
  pdf.rect(0, 0, pageWidth, pageHeight, 'F');
};

const drawTopAccentBar = (pdf: jsPDF) => {
  const pageWidth = pdf.internal.pageSize.getWidth();
  pdf.setFillColor(...COLOR.heading);
  pdf.rect(0, 0, pageWidth, 6, 'F');
};

const drawHeadingDivider = (pdf: jsPDF, margin: number, y: number) => {
  const pageWidth = pdf.internal.pageSize.getWidth();
  pdf.setDrawColor(...COLOR.divider);
  pdf.setLineWidth(1.5);
  pdf.line(margin, y, pageWidth - margin, y);
};

const ensureSpace = (pdf: jsPDF, currentY: number, needed: number, margin: number) => {
  const pageHeight = pdf.internal.pageSize.getHeight();
  if (currentY + needed > pageHeight - margin) {
    pdf.addPage();
    paintPageBackground(pdf);
    drawTopAccentBar(pdf);
    return margin;
  }
  return currentY;
};

const toBase64 = (bytes: Uint8Array): string => {
  let binary = '';
  const chunkSize = 0x8000;
  for (let i = 0; i < bytes.length; i += chunkSize) {
    binary += String.fromCharCode(...bytes.subarray(i, i + chunkSize));
  }
  return btoa(binary);
};

const fetchFontBase64 = async (url: string): Promise<string> => {
  const cached = fontBase64Cache.get(url);
  if (cached) return cached;

  const response = await fetch(url);
  if (!response.ok) {
    throw new Error(`Failed to load font asset: ${url}`);
  }

  const arrayBuffer = await response.arrayBuffer();
  const base64 = toBase64(new Uint8Array(arrayBuffer));
  fontBase64Cache.set(url, base64);
  return base64;
};

const ensureRobotoFonts = async (pdf: jsPDF) => {
  if (robotoRegistered) return;

  const [regularBase64, boldBase64, italicBase64] = await Promise.all([
    fetchFontBase64(robotoRegularTtfUrl),
    fetchFontBase64(robotoBoldTtfUrl),
    fetchFontBase64(robotoItalicTtfUrl),
  ]);

  pdf.addFileToVFS(FONT.regularFile, regularBase64);
  pdf.addFileToVFS(FONT.boldFile, boldBase64);
  pdf.addFileToVFS(FONT.italicFile, italicBase64);

  pdf.addFont(FONT.regularFile, FONT.family, 'normal');
  pdf.addFont(FONT.boldFile, FONT.family, 'bold');
  pdf.addFont(FONT.italicFile, FONT.family, 'italic');

  robotoRegistered = true;
};

const drawHeatmap = (
  pdf: jsPDF,
  x: number,
  y: number,
  availableWidth: number,
  operationalScore: number,
  strategicScore: number,
) => {
  const tierLabels = ['MANUAL', 'SILOED', 'MANAGED', 'CONTROLLED', 'AUTONOMOUS'];
  const xTicks = [6, 10, 14, 18, 22, 26, 30];
  const yTicks = [25, 22, 18, 14, 10, 6];
  const cols = MATRIX.maxOp - MATRIX.minOp + 1;
  const rows = MATRIX.maxStrat - MATRIX.minStrat + 1;
  const leftAxisWidth = 28;
  const topHeaderHeight = 20;
  const bottomTicksHeight = 16;
  const bottomAxisLabelHeight = 20;
  const chartWidth = availableWidth - leftAxisWidth;
  const cellWidth = chartWidth / cols;
  const cellHeight = cellWidth * 0.8;
  const chartHeight = rows * cellHeight;
  const chartX = x + leftAxisWidth;
  const chartY = y + topHeaderHeight;

  const opScore = clamp(Math.round(operationalScore), MATRIX.minOp, MATRIX.maxOp);
  const stratScore = clamp(Math.round(strategicScore), MATRIX.minStrat, MATRIX.maxStrat);

  pdf.setFont(FONT.family, 'bold');
  pdf.setFontSize(10);
  pdf.setTextColor(...COLOR.body);
  tierLabels.forEach((label, index) => {
    const bandStartX = chartX + index * 5 * cellWidth;
    const bandCenterX = bandStartX + (5 * cellWidth) / 2;
    pdf.text(label, bandCenterX, y + 12, { align: 'center' });
  });

  for (let row = 0; row < rows; row += 1) {
    const stratValue = MATRIX.maxStrat - row;
    for (let col = 0; col < cols; col += 1) {
      const opValue = MATRIX.minOp + col;
      const tier = getMatrixTier(opValue, stratValue);
      const [r, g, b] = getTierFill(tier);
      pdf.setFillColor(r, g, b);
      pdf.rect(chartX + col * cellWidth, chartY + row * cellHeight, cellWidth, cellHeight, 'F');
    }
  }

  pdf.setDrawColor(...COLOR.grid);
  pdf.setLineWidth(0.3);
  for (let col = 0; col <= cols; col += 1) {
    const lineX = chartX + col * cellWidth;
    pdf.line(lineX, chartY, lineX, chartY + chartHeight);
  }
  for (let row = 0; row <= rows; row += 1) {
    const lineY = chartY + row * cellHeight;
    pdf.line(chartX, lineY, chartX + chartWidth, lineY);
  }

  pdf.setDrawColor(...COLOR.grid);
  pdf.setLineWidth(0.6);
  pdf.rect(chartX, chartY, chartWidth, chartHeight);

  pdf.setFont(FONT.family, 'normal');
  pdf.setFontSize(10);
  pdf.setTextColor(...COLOR.body);
  yTicks.forEach((tick) => {
    const tickY = chartY + (MATRIX.maxStrat - tick + 0.5) * cellHeight + 3;
    pdf.text(String(tick), chartX - 10, tickY, { align: 'right' });
  });

  xTicks.forEach((tick) => {
    const isEdge = tick === MATRIX.minOp || tick === MATRIX.maxOp;
    const tickX = chartX + (tick - MATRIX.minOp) * cellWidth;
    const align = isEdge ? (tick === MATRIX.minOp ? 'left' : 'right') : 'center';
    pdf.text(String(tick), tickX, chartY + chartHeight + 14, { align });
  });

  pdf.setFont(FONT.family, 'bold');
  pdf.setFontSize(11);
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Operational maturity', chartX + chartWidth / 2, chartY + chartHeight + bottomTicksHeight + bottomAxisLabelHeight - 2, { align: 'center' });
  pdf.text('Strategic maturity', x + 8, chartY + chartHeight / 2, { angle: 90, align: 'center' });

  const markerX = chartX + (opScore - MATRIX.minOp + 0.5) * cellWidth;
  const markerY = chartY + (MATRIX.maxStrat - stratScore + 0.5) * cellHeight;
  const outerRadius = Math.min(cellWidth, cellHeight) * 0.36;

  pdf.setFillColor(...COLOR.markerRed);
  pdf.circle(markerX, markerY, outerRadius, 'F');
  pdf.setFillColor(255, 255, 255);
  pdf.circle(markerX, markerY, outerRadius * 0.4, 'F');

  return topHeaderHeight + chartHeight + bottomTicksHeight + bottomAxisLabelHeight;
};

export async function generateMaturityPdfReport({
  answers,
  maturityResults,
  segmentData,
  actionsData,
  questions,
  clientName,
}: PdfInput): Promise<void> {
  const pdf = new jsPDF({ orientation: 'portrait', unit: 'pt', format: 'a4' });
  await ensureRobotoFonts(pdf);

  paintPageBackground(pdf);
  pdf.setFont(FONT.family, 'normal');

  const pageWidth = pdf.internal.pageSize.getWidth();
  const pageHeight = pdf.internal.pageSize.getHeight();
  const margin = 40;
  const centerX = pageWidth / 2;

  const exportDate = new Date().toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
  });

  const operationalScore = maturityResults.operationalScore;
  const strategicScore = maturityResults.strategicScore;
  const operationalPercent = ((operationalScore / 30) * 100).toFixed(1);
  const strategicPercent = ((strategicScore / 25) * 100).toFixed(1);
  const strategicLabel = getBandDescription(maturityResults.strategicBand);

  const drawMetricCard = (
    cardX: number,
    cardY: number,
    cardWidth: number,
    title: string,
    value: string,
    subtitle: string,
    progress?: number,
  ) => {
    const cardHeight = 112;

    pdf.setFillColor(...COLOR.cardBg);
    pdf.roundedRect(cardX, cardY, cardWidth, cardHeight, 8, 8, 'F');

    pdf.setFont(FONT.family, 'normal');
    pdf.setFontSize(10);
    pdf.setTextColor(...COLOR.midGray);
    pdf.text(title, cardX + 14, cardY + 22);

    pdf.setFont(FONT.family, 'bold');
    pdf.setFontSize(22);
    pdf.setTextColor(...COLOR.heading);
    pdf.text(value, cardX + 14, cardY + 52);

    if (typeof progress === 'number') {
      const barX = cardX + 14;
      const barY = cardY + 64;
      const barWidth = cardWidth - 28;
      const barHeight = 7;
      const fillWidth = Math.max(0, Math.min(1, progress)) * barWidth;

      pdf.setFillColor(209, 213, 219);
      pdf.roundedRect(barX, barY, barWidth, barHeight, 3.5, 3.5, 'F');
      pdf.setFillColor(...COLOR.heading);
      pdf.roundedRect(barX, barY, fillWidth, barHeight, 3.5, 3.5, 'F');
    }

    pdf.setFont(FONT.family, 'normal');
    pdf.setFontSize(10);
    pdf.setTextColor(...COLOR.body);
    const subtitleLines = splitParagraph(pdf, subtitle, cardWidth - 28);
    subtitleLines.slice(0, 2).forEach((line: string, idx: number) => {
      const subtitleY = typeof progress === 'number' ? cardY + 90 : cardY + 76;
      pdf.text(line, cardX + 14, subtitleY + idx * 12);
    });
  };

  // Cover page
  pdf.setFontSize(28);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('R2R Maturity Assessment', centerX, 140, { align: 'center' });

  pdf.setFontSize(20);
  pdf.setFont(FONT.family, 'normal');
  pdf.setTextColor(...COLOR.midGray);
  pdf.text('Results Report', centerX, 180, { align: 'center' });

  pdf.setFontSize(18);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text(clientName, centerX, 240, { align: 'center' });

  const positionText = `${maturityResults.operationalBand} Operations  ${maturityResults.strategicBand} Strategic`;
  pdf.setFontSize(14);
  pdf.setFont(FONT.family, 'normal');
  pdf.setTextColor(...COLOR.body);
  pdf.text(positionText, centerX, 280, { align: 'center' });

  pdf.setFontSize(12);
  pdf.setTextColor(...COLOR.midGray);
  pdf.text(`Generated: ${exportDate}`, centerX, 320, { align: 'center' });

  // Executive Summary
  pdf.addPage();
  paintPageBackground(pdf);
  drawTopAccentBar(pdf);
  let y = margin;

  pdf.setFontSize(22);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Executive Summary', margin, y);
  drawHeadingDivider(pdf, margin, y + 10);
  y += 40;

  const cardGap = 12;
  const cardWidth = (pageWidth - 2 * margin - cardGap * 2) / 3;
  drawMetricCard(
    margin,
    y,
    cardWidth,
    'Strategic Position',
    maturityResults.strategicBand,
    strategicLabel,
  );
  drawMetricCard(
    margin + cardWidth + cardGap,
    y,
    cardWidth,
    'Operational Score',
    `${operationalScore} / 30`,
    `${operationalPercent}%`,
    operationalScore / 30,
  );
  drawMetricCard(
    margin + (cardWidth + cardGap) * 2,
    y,
    cardWidth,
    'Strategic Score',
    `${strategicScore} / 25`,
    `${strategicPercent}%`,
    strategicScore / 25,
  );
  y += 132;

  pdf.setFontSize(16);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Maturity Position Matrix', margin, y);
  y += 20;

  const availableWidth = pageWidth - 2 * margin;
  const matrixHeight = drawHeatmap(pdf, margin, y, availableWidth, operationalScore, strategicScore);
  y += matrixHeight + 30;

  pdf.setFontSize(16);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Strategic Position', margin, y);
  y += 22;

  pdf.setFontSize(12);
  pdf.setFont(FONT.family, 'normal');
  pdf.setTextColor(...COLOR.body);
  const summaryText = `This organization demonstrates ${maturityResults.operationalBand.toLowerCase()} operational capabilities combined with ${maturityResults.strategicBand.toLowerCase()} strategic alignment.`;
  const summaryLines = splitParagraph(pdf, summaryText, pageWidth - 2 * margin);
  summaryLines.forEach((line: string) => {
    pdf.text(line, margin, y);
    y += 16;
  });

  // Strategic Analysis
  if (segmentData) {
    pdf.addPage();
    paintPageBackground(pdf);
    drawTopAccentBar(pdf);
    y = margin;

    pdf.setFontSize(22);
    pdf.setFont(FONT.family, 'bold');
    pdf.setTextColor(...COLOR.heading);
    pdf.text('Strategic Analysis', margin, y);
    drawHeadingDivider(pdf, margin, y + 10);
    y += 40;

    pdf.setFontSize(16);
    pdf.setFont(FONT.family, 'bold');
    pdf.setTextColor(...COLOR.heading);
    pdf.text(`Position: ${segmentData.segment_name}`, margin, y);
    y += 24;

    pdf.setFontSize(12);
    pdf.setFont(FONT.family, 'normal');
    pdf.setTextColor(...COLOR.body);
    if (segmentData.summary_diagnosis) {
      const diagnosisLines = splitParagraph(pdf, segmentData.summary_diagnosis, pageWidth - 2 * margin);
      diagnosisLines.forEach((line: string) => {
        pdf.text(line, margin, y);
        y += 15;
      });
      y += 10;
    }

    if (segmentData.symptoms) {
      y = ensureSpace(pdf, y, 60, margin);
      pdf.setFontSize(14);
      pdf.setFont(FONT.family, 'bold');
      pdf.setTextColor(...COLOR.heading);
      pdf.text('Key Symptoms:', margin, y);
      y += 20;

      pdf.setFontSize(12);
      pdf.setFont(FONT.family, 'normal');
      pdf.setTextColor(...COLOR.body);
      const symptomLines = splitParagraph(pdf, segmentData.symptoms, pageWidth - 2 * margin);
      symptomLines.forEach((line: string) => {
        pdf.text(line, margin, y);
        y += 15;
      });
      y += 8;
    }

    if (segmentData.opportunities) {
      y = ensureSpace(pdf, y, 60, margin);
      pdf.setFontSize(14);
      pdf.setFont(FONT.family, 'bold');
      pdf.setTextColor(...COLOR.heading);
      pdf.text('Opportunities:', margin, y);
      y += 20;

      pdf.setFontSize(12);
      pdf.setFont(FONT.family, 'normal');
      pdf.setTextColor(...COLOR.body);
      const opportunityLines = splitParagraph(pdf, segmentData.opportunities, pageWidth - 2 * margin);
      opportunityLines.forEach((line: string) => {
        pdf.text(line, margin, y);
        y += 15;
      });
    }
  }

  // Recommended Actions
  if (actionsData.length > 0) {
    pdf.addPage();
    paintPageBackground(pdf);
    drawTopAccentBar(pdf);
    y = margin;

    pdf.setFontSize(22);
    pdf.setFont(FONT.family, 'bold');
    pdf.setTextColor(...COLOR.heading);
    pdf.text('Recommended Actions', margin, y);
    drawHeadingDivider(pdf, margin, y + 10);
    y += 40;

    actionsData.forEach((action, index) => {
      y = ensureSpace(pdf, y, 110, margin);

      pdf.setFontSize(14);
      pdf.setFont(FONT.family, 'bold');
      pdf.setTextColor(...COLOR.heading);
      pdf.text(`${index + 1}. ${action.action_title}`, margin, y);
      y += 20;

      pdf.setFontSize(11);
      pdf.setFont(FONT.family, 'normal');
      pdf.setTextColor(...COLOR.body);
      const actionDetails = `Type: ${action.action_type || 'N/A'} | Timeline: ${action.time_horizon || 'N/A'}\n` +
        `Rationale: ${action.action_rationale || 'N/A'}\n` +
        `Expected Outcome: ${action.expected_outcome || 'N/A'}`;
      const actionLines = splitParagraph(pdf, actionDetails, pageWidth - 2 * margin);
      actionLines.forEach((line: string) => {
        pdf.text(line, margin, y);
        y += 12;
      });
      y += 12;
    });
  }

  // Appendix
  pdf.addPage();
  paintPageBackground(pdf);
  drawTopAccentBar(pdf);
  y = margin;

  pdf.setFontSize(22);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Appendix: Maturity Assessment Responses', margin, y);
  drawHeadingDivider(pdf, margin, y + 10);
  y += 40;

  pdf.setFontSize(16);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Operational Maturity Questions (1-6)', margin, y);
  y += 25;

  for (let i = 0; i < 6; i += 1) {
    y = ensureSpace(pdf, y, 90, margin);
    const score = answers[i] || 0;
    const question = questions[i];
    const selectedOption = getSelectedOptionText(questions, i, score);

    pdf.setFontSize(12);
    pdf.setFont(FONT.family, 'bold');
    pdf.setTextColor(...COLOR.heading);
    pdf.text(`Q${i + 1}: Score ${score}/5`, margin, y);
    y += 16;

    pdf.setFontSize(11);
    pdf.setFont(FONT.family, 'normal');
    pdf.setTextColor(...COLOR.body);
    if (question?.title) {
      const questionLines = splitParagraph(pdf, question.title, pageWidth - 2 * margin);
      questionLines.forEach((line: string) => {
        pdf.text(line, margin, y);
        y += 12;
      });
      y += 4;
    }

    pdf.setTextColor(...COLOR.heading);
    pdf.setFont(FONT.family, 'italic');
    const responseLines = splitParagraph(pdf, `Response: ${selectedOption}`, pageWidth - 2 * margin);
    responseLines.forEach((line: string) => {
      pdf.text(line, margin, y);
      y += 12;
    });
    y += 12;
  }

  y += 10;
  y = ensureSpace(pdf, y, 30, margin);

  pdf.setFontSize(16);
  pdf.setFont(FONT.family, 'bold');
  pdf.setTextColor(...COLOR.heading);
  pdf.text('Strategic Maturity Questions (7-11)', margin, y);
  y += 25;

  for (let i = 6; i < 11; i += 1) {
    y = ensureSpace(pdf, y, 90, margin);
    const score = answers[i] || 0;
    const question = questions[i];
    const selectedOption = getSelectedOptionText(questions, i, score);

    pdf.setFontSize(12);
    pdf.setFont(FONT.family, 'bold');
    pdf.setTextColor(...COLOR.heading);
    pdf.text(`Q${i + 1}: Score ${score}/5`, margin, y);
    y += 16;

    pdf.setFontSize(11);
    pdf.setFont(FONT.family, 'normal');
    pdf.setTextColor(...COLOR.body);
    if (question?.title) {
      const questionLines = splitParagraph(pdf, question.title, pageWidth - 2 * margin);
      questionLines.forEach((line: string) => {
        pdf.text(line, margin, y);
        y += 12;
      });
      y += 4;
    }

    pdf.setTextColor(...COLOR.heading);
    pdf.setFont(FONT.family, 'italic');
    const responseLines = splitParagraph(pdf, `Response: ${selectedOption}`, pageWidth - 2 * margin);
    responseLines.forEach((line: string) => {
      pdf.text(line, margin, y);
      y += 12;
    });
    y += 12;
  }

  // Footer
  const footerY = pageHeight - 30;
  pdf.setFontSize(9);
  pdf.setFont(FONT.family, 'normal');
  pdf.setTextColor(...COLOR.footer);
  const footerText = ' Redwood Software, Inc. All rights reserved. All trademarks are the property of their respective owners.';
  pdf.text(footerText, centerX, footerY, { align: 'center' });

  pdf.save(`maturity-assessment-${new Date().toISOString().split('T')[0]}.pdf`);
}
