/**
 * Maturity Assessment Questions - Single Source of Truth
 *
 * This file contains the canonical definition of all maturity assessment questions.
 * All other components should import from this file to maintain consistency.
 */

export interface MaturityQuestion {
  section: "Operational Excellence" | "Strategic Alignment";
  title: string;
  options: string[];
}

