// Helper function to get strategic alignment questions only
export const getStrategicAlignmentQuestions = (): MaturityQuestion[] => {
  return maturityQuestions.filter(q => q.section === "Strategic Alignment");
};

// Helper function to get operational excellence questions only
export const getOperationalExcellenceQuestions = (): MaturityQuestion[] => {
  return maturityQuestions.filter(q => q.section === "Operational Excellence");
};

// Export legacy questions format for backward compatibility
export const questions = maturityQuestions;

// Pre-defined implementation blocker options
export const blockersOptions = [
  "Limited technology infrastructure",
  "Resistance to change from stakeholders",
  "Insufficient budget for transformation",
  "Lack of skilled personnel",
  "Regulatory compliance requirements",
  "Data quality and integration challenges",
  "Vendor dependency and lock-in",
  "Complex organizational structure",
  "Legacy system constraints",
  "Time constraints and competing priorities"
];
