import React from 'react';
import ReactDOM from 'react-dom/client';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import App from './App';
import './index.css';

// Create a client for React Query
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: 1000 * 60 * 5, // 5 minutes
      retry: 1,
      refetchOnWindowFocus: false,
    },
  },
});

// Get the root element - support both standalone and WordPress embedded modes
const getRootElement = (): HTMLElement => {
  // Try WordPress shortcode container first
  const wpRoot = document.getElementById('maturity-assessment-root');
  if (wpRoot) return wpRoot;

  // Fall back to standard root
  const root = document.getElementById('root');
  if (root) return root;

  // Create root if none exists (shouldn't happen in normal usage)
  const newRoot = document.createElement('div');
  newRoot.id = 'root';
  document.body.appendChild(newRoot);
  return newRoot;
};

// Extract session ID from data attribute or URL
const getInitialSessionId = (): string | null => {
  const rootElement = document.getElementById('maturity-assessment-root');
  if (rootElement?.dataset.sessionId) {
    return rootElement.dataset.sessionId;
  }

  // Check URL for session parameter
  const urlParams = new URLSearchParams(window.location.search);
  return urlParams.get('session') || urlParams.get('s');
};

// Initialize the app
const rootElement = getRootElement();
const initialSessionId = getInitialSessionId();

ReactDOM.createRoot(rootElement).render(
  <React.StrictMode>
    <QueryClientProvider client={queryClient}>
      <App initialSessionId={initialSessionId} />
    </QueryClientProvider>
  </React.StrictMode>
);
