/**
 * Static Reference Data for Maturity Assessment
 *
 * This data is bundled directly in the frontend - no backend database needed.
 * Contains: segments, question insights, strategic themes, and recommended actions.
 */

// Maturity bands
const BANDS = ['Manual', 'Siloed', 'Managed', 'Controlled', 'Autonomous'] as const;

export type MaturityBand = typeof BANDS[number];

export interface MaturitySegment {
  id: string;
  operational_band: string;
  strategic_band: string;
  segment_name: string;
  headline_message: string;
  summary_diagnosis: string;
  strategic_theme: string;
  symptoms: string;
  risks: string;
  opportunities: string;
  highlighted_theme_ids: string[];
}

export interface QuestionInsight {
  id: string;
  question_text: string;
  dimension: string;
  low_score_flag: string;
  narrative_impact: string;
  strategic_theme: string;
  trigger_score_threshold: number;
  impact_weight: number;
  theme_id: string;
}

export interface StrategicTheme {
  theme_tag: string;
  theme_title: string;
  description: string;
  why_it_matters: string;
  redwood_position: string;
  example_indicators: string[];
  icon_name: string;
  display_order: number;
}

export interface RecommendedAction {
  id: string;
  segment_id: string;
  theme_tag: string;
  theme_id: string;
  action_title: string;
  action_rationale: string;
  expected_outcome: string;
  action_type: string;
  time_horizon: string;
  effort_level: string;
  owner_role: string;
  priority_score: number;
  applies_when: string;
  notes: string;
}

// Helper functions for generating segment data
function getHeadlineMessage(opBand: string, stratBand: string): string {
  const opLevel = BANDS.indexOf(opBand as MaturityBand);
  const stratLevel = BANDS.indexOf(stratBand as MaturityBand);
  const avgLevel = Math.round((opLevel + stratLevel) / 2);

  const messages = [
    'Foundation building phase - significant transformation opportunity ahead',
    'Early automation journey - building blocks in place but gaps remain',
    'Progressive maturity - selective automation delivering value',
    'Advanced operations - orchestration and governance well established',
    'Industry-leading maturity - continuous optimization and predictive capabilities'
  ];

  return messages[avgLevel];
}

function getSummaryDiagnosis(opBand: string, stratBand: string): string {
  return `Your organization demonstrates ${opBand.toLowerCase()} operational capabilities with ${stratBand.toLowerCase()} strategic alignment. This positioning indicates specific opportunities for improvement in process automation and governance frameworks.`;
}

function getStrategicTheme(opBand: string, stratBand: string): string {
  const opLevel = BANDS.indexOf(opBand as MaturityBand);
  const stratLevel = BANDS.indexOf(stratBand as MaturityBand);

  if (opLevel > stratLevel) return 'Strategic Alignment Gap';
  if (stratLevel > opLevel) return 'Operational Excellence Focus';
  return 'Balanced Maturity';
}

function getSymptoms(opBand: string): string {
  const opLevel = BANDS.indexOf(opBand as MaturityBand);
  const symptoms = [
    'Heavy reliance on manual processes, fragmented data sources, limited visibility into close status',
    'Departmental automation silos, inconsistent process execution, reactive exception handling',
    'Partial automation coverage, standardized processes with manual gaps, periodic bottlenecks',
    'Integrated workflows, proactive monitoring, occasional optimization opportunities',
    'Touchless operations, predictive insights, continuous improvement culture'
  ];
  return symptoms[opLevel];
}

function getRisks(opBand: string): string {
  const opLevel = BANDS.indexOf(opBand as MaturityBand);
  const risks = [
    'High error rates, compliance exposure, resource burnout, extended close cycles',
    'Data inconsistencies, audit challenges, scalability limitations, talent dependency',
    'Process variations, integration gaps, change management friction, partial visibility',
    'Technology debt, governance complexity, vendor dependencies, skill gaps',
    'Complacency risk, innovation fatigue, market disruption vulnerability'
  ];
  return risks[opLevel];
}

function getOpportunities(opBand: string): string {
  const opLevel = BANDS.indexOf(opBand as MaturityBand);
  const opportunities = [
    'Quick wins through basic automation, process standardization, control improvements',
    'Integration initiatives, workflow automation, governance framework establishment',
    'End-to-end orchestration, real-time monitoring, advanced analytics adoption',
    'AI/ML implementation, predictive capabilities, continuous optimization programs',
    'Industry leadership, innovation partnerships, next-generation finance operations'
  ];
  return opportunities[opLevel];
}

// Generate all 25 segment combinations
export const segments: MaturitySegment[] = BANDS.flatMap(opBand =>
  BANDS.map(stratBand => ({
    id: `${opBand}_${stratBand}`,
    operational_band: opBand,
    strategic_band: stratBand,
    segment_name: `${opBand}-${stratBand}`,
    headline_message: getHeadlineMessage(opBand, stratBand),
    summary_diagnosis: getSummaryDiagnosis(opBand, stratBand),
    strategic_theme: getStrategicTheme(opBand, stratBand),
    symptoms: getSymptoms(opBand),
    risks: getRisks(opBand),
    opportunities: getOpportunities(opBand),
    highlighted_theme_ids: []
  }))
);

