// Generate additional actions for each segment
function generateSegmentActions(): RecommendedAction[] {
  const allActions = [...baseActions];

  BANDS.forEach((opBand, opIdx) => {
    BANDS.forEach((stratBand, stratIdx) => {
      const segmentId = `${opBand}_${stratBand}`;
      const avgLevel = Math.round((opIdx + stratIdx) / 2);

      // Skip segments that already have actions
      const existingActions = allActions.filter(a => a.segment_id === segmentId);
      if (existingActions.length >= 2) return;

      // Add generic actions based on maturity level
      if (avgLevel < 2) {
        allActions.push({
          id: `ACT_${segmentId}_001`,
          segment_id: segmentId,
          theme_tag: 'automation-gap',
          theme_id: 'automation-gap',
          action_title: 'Assess automation readiness',
          action_rationale: 'Identify high-value automation opportunities across R2R processes.',
          expected_outcome: 'Prioritized automation roadmap aligned to business value.',
          action_type: 'Foundation',
          time_horizon: '0-90 days',
          effort_level: 'Low',
          owner_role: 'Finance Director',
          priority_score: 80 - avgLevel * 10,
          applies_when: 'segment_only',
          notes: ''
        });
      } else if (avgLevel < 4) {
        allActions.push({
          id: `ACT_${segmentId}_001`,
          segment_id: segmentId,
          theme_tag: 'orchestration-gap',
          theme_id: 'orchestration-gap',
          action_title: 'Expand automation coverage',
          action_rationale: 'Build on existing automation to cover more R2R processes.',
          expected_outcome: 'Increased touchless processing rate and reduced cycle time.',
          action_type: 'Scale',
          time_horizon: '3-6 months',
          effort_level: 'Medium',
          owner_role: 'Finance Director',
          priority_score: 70 - avgLevel * 5,
          applies_when: 'segment_only',
          notes: ''
        });
      } else {
        allActions.push({
          id: `ACT_${segmentId}_001`,
          segment_id: segmentId,
          theme_tag: 'analytics',
          theme_id: 'analytics',
          action_title: 'Implement continuous optimization',
          action_rationale: 'Use analytics to identify ongoing improvement opportunities.',
          expected_outcome: 'Sustained efficiency gains through data-driven refinement.',
          action_type: 'Analytics',
          time_horizon: '6-12 months',
          effort_level: 'Medium',
          owner_role: 'CFO',
          priority_score: 60 - avgLevel * 5,
          applies_when: 'segment_only',
          notes: ''
        });
      }
    });
  });

  return allActions;
}

export const recommendedActions: RecommendedAction[] = generateSegmentActions();

// Helper function to get segment by bands
export function getSegmentByBands(operationalBand: string, strategicBand: string): MaturitySegment | undefined {
  return segments.find(
    s => s.operational_band === operationalBand && s.strategic_band === strategicBand
  );
}

// Helper function to get actions for a segment
export function getActionsForSegment(segmentId: string): RecommendedAction[] {
  return recommendedActions
    .filter(a => a.segment_id === segmentId)
    .sort((a, b) => b.priority_score - a.priority_score);
}

// Helper function to get theme by tag
export function getThemeByTag(themeTag: string): StrategicTheme | undefined {
  return strategicThemes.find(t => t.theme_tag === themeTag);
}
