/**
 * Maturity Assessment Results Screen
 *
 * Displays assessment results including:
 * - Operational and Strategic maturity scoring
 * - Maturity positioning matrix (2D heatmap)
 * - Strategic analysis (symptoms, risks, opportunities)
 * - Recommended actions
 * - Implementation blockers
 * - Detailed responses (collapsible)
 *
 * Completely client-side - NO backend/database required
 * Uses localStorage for assessment data and bundled reference data
 */

import { useEffect, useState } from 'react';
import { useLocation } from 'wouter';
import {
  Download,
  AlertTriangle,
  Lightbulb,
  Shield,
  TrendingUp,
  Users,
  BarChart3,
  CheckCircle,
  RefreshCw,
  User,
  Building2,
  Settings,
  Target,
  Bot
} from 'lucide-react';
import {
  generateMaturityResults,
  calculateScoresFromAnswers,
  getBandDescription
} from '@/utils/maturityBanding';
import { useParentCommunication } from '@/hooks/useParentCommunication';
import {
  MaturitySegmentDisplay,
  SectionHeader
} from '@/components/maturity';
import {
  getSegmentByBands,
  getActionsForSegment,
  type MaturitySegment,
  type RecommendedAction
} from '@/data/referenceData';
import { maturityQuestions } from '@/constants/maturityQuestions';
import { generateMaturityPdfReport } from '@/utils/maturityPdfReport';
import type { MaturityResults as MaturityResultsType } from '@shared/types';

interface MaturityResultsProps {
  sessionId: string | null;
}

interface StoredAssessment {
  sessionId: string;
  answers: number[];
  blockers: string[];
  otherBlockers: string;
  completedAt: string;
}

// Icon mapping for action types
const getActionIcon = (actionType: string) => {
  switch (actionType?.toLowerCase()) {
    case 'foundation': return Shield;
    case 'quick win': return TrendingUp;
    case 'governance': return Users;
    case 'analytics': return BarChart3;
    case 'scale': return TrendingUp;
    default: return CheckCircle;
  }
};

// Tier icon based on maturity level
const getTierIcon = (tier: string) => {
  switch (tier?.toLowerCase()) {
    case 'autonomous': return TrendingUp;
    case 'controlled': return Shield;
    case 'managed': return Users;
    case 'siloed': return BarChart3;
    default: return AlertTriangle;
  }
};

export default function MaturityResults({ sessionId }: MaturityResultsProps) {
  useParentCommunication();
  const [, setLocation] = useLocation();
  const [maturityResults, setMaturityResults] = useState<MaturityResultsType | null>(null);
  const [assessmentData, setAssessmentData] = useState<StoredAssessment | null>(null);
  const [segmentData, setSegmentData] = useState<MaturitySegment | null>(null);
  const [actionsData, setActionsData] = useState<RecommendedAction[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [showDetailedResponses, setShowDetailedResponses] = useState(false);
  const [isGeneratingPdf, setIsGeneratingPdf] = useState(false);

  // Generate PDF report (structured, vector PDF)
  const handleDownloadPdf = async () => {
    if (isGeneratingPdf || !assessmentData || !maturityResults) return;

    setIsGeneratingPdf(true);

    try {
      const clientName = 'Assessment Report';
      const topActions = [...actionsData]
        .sort((a, b) => (b.priority_score || 0) - (a.priority_score || 0))
        .slice(0, 4);

      await generateMaturityPdfReport({
        answers: assessmentData.answers,
        maturityResults,
        segmentData,
        actionsData: topActions,
        questions: maturityQuestions,
        clientName,
      });
    } catch (error) {
      console.error('PDF generation failed:', error);
    } finally {
      setIsGeneratingPdf(false);
    }
  };

  // Get session ID from URL if not provided
  const urlParams = new URLSearchParams(window.location.search);
  const effectiveSessionId = sessionId || urlParams.get('session') || urlParams.get('s');

  // Load assessment data from localStorage
  useEffect(() => {
    if (!effectiveSessionId) {
      setIsLoading(false);
      return;
    }

    const storedData = localStorage.getItem(`assessment_${effectiveSessionId}`);
    if (storedData) {
      try {
        const parsed = JSON.parse(storedData) as StoredAssessment;
        setAssessmentData(parsed);

        // Calculate maturity results
        const { operationalScore, strategicScore } = calculateScoresFromAnswers(parsed.answers);
        const results = generateMaturityResults(operationalScore, strategicScore);
        setMaturityResults(results);

        // Get segment data from bundled reference data
        const segment = getSegmentByBands(results.operationalBand, results.strategicBand);
        if (segment) {
          setSegmentData(segment);

          // Get actions for this segment
          const actions = getActionsForSegment(segment.id);
          setActionsData(actions);
        }
      } catch (e) {
        console.error('Failed to parse assessment data:', e);
      }
    }

    setIsLoading(false);
  }, [effectiveSessionId]);

  // Loading state
  if (isLoading) {
    return (
      <div className="min-h-screen bg-slate-900 flex items-center justify-center p-4">
        <div className="text-center">
          <RefreshCw className="w-8 h-8 text-teal-400 animate-spin mx-auto mb-4" />
          <p className="text-slate-400">Loading results...</p>
        </div>
      </div>
    );
  }

  // No session or data found
  if (!effectiveSessionId || !assessmentData) {
    return (
      <div className="min-h-screen bg-slate-900 flex items-center justify-center p-4">
        <div className="text-center max-w-md mx-auto p-6">
          <AlertTriangle className="w-12 h-12 text-amber-400 mx-auto mb-4" />
          <h2 className="text-xl font-bold text-white mb-2">Assessment Not Found</h2>
          <p className="text-slate-400 mb-6">
            We couldn't find your assessment results. Please complete an assessment first.
          </p>
          <button
            onClick={() => setLocation('/assessment')}
            className="btn btn-primary"
          >
            Start New Assessment
          </button>
        </div>
      </div>
    );
  }

  if (!maturityResults) {
    return (
      <div className="min-h-screen bg-slate-900 flex items-center justify-center p-4">
        <div className="text-center">
          <RefreshCw className="w-8 h-8 text-teal-400 animate-spin mx-auto mb-4" />
          <p className="text-slate-400">Calculating results...</p>
        </div>
      </div>
    );
  }

  const { operationalBand, operationalScore, strategicScore } = maturityResults;
  const currentTier = operationalBand; // Use operational band as the primary tier
  const TierIcon = getTierIcon(currentTier);

  return (
    <div className="min-h-screen bg-slate-900 p-4 sm:p-6 lg:p-8">
      <div className="max-w-[1400px] mx-auto space-y-8">
        {/* Page Header */}
        <div className="flex flex-col sm:flex-row items-center justify-between gap-4 mb-8">
          <div className="text-center sm:text-left">
            <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold text-white mb-2">
              Maturity Assessment Results
            </h1>
            <p className="text-slate-400">
              R2R Maturity Assessment for Your Organization
            </p>
          </div>
          {/* Hide button during PDF generation so it doesn't appear in the PDF */}
          {!isGeneratingPdf && (
            <button
              onClick={handleDownloadPdf}
              className="btn btn-primary flex items-center gap-2"
            >
              <Download className="w-4 h-4" />
              Download PDF
            </button>
          )}
        </div>

        {/* Summary Header Cards */}
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
          <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
            <div className="flex items-center gap-3 mb-3">
              <div className="p-2 bg-teal-500/20 rounded-lg">
                <TierIcon className="w-5 h-5 text-teal-400" />
              </div>
              <span className="text-sm text-slate-400 font-medium">Strategic Position</span>
            </div>
            <div className="text-2xl font-bold text-teal-400 mb-1">{currentTier}</div>
            <p className="text-sm text-slate-500">{getBandDescription(currentTier)}</p>
          </div>

          <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
            <div className="flex items-center gap-3 mb-3">
              <span className="text-sm text-slate-400 font-medium">Operational Score</span>
            </div>
            <div className="text-2xl font-bold text-white mb-1">{operationalScore} / 30</div>
            <div className="w-full bg-slate-700 rounded-full h-2 mt-2">
              <div
                className="bg-teal-500 rounded-full h-2 transition-all"
                style={{ width: `${(operationalScore / 30) * 100}%` }}
              />
            </div>
          </div>

          <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
            <div className="flex items-center gap-3 mb-3">
              <span className="text-sm text-slate-400 font-medium">Strategic Score</span>
            </div>
            <div className="text-2xl font-bold text-white mb-1">{strategicScore} / 25</div>
            <div className="w-full bg-slate-700 rounded-full h-2 mt-2">
              <div
                className="bg-teal-500 rounded-full h-2 transition-all"
                style={{ width: `${(strategicScore / 25) * 100}%` }}
              />
            </div>
          </div>
        </div>

        {/* Maturity Positioning Matrix */}
        <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
          <SectionHeader
            title="R2R Maturity Positioning"
            subtitle="Your organization's position on the operational vs strategic maturity matrix"
          />
          <MaturitySegmentDisplay
            operationalScore={operationalScore}
            strategicScore={strategicScore}
          />
        </div>

        {/* Strategic Analysis */}
        {segmentData && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
              <div className="flex items-center gap-2 mb-4">
                <AlertTriangle className="w-5 h-5 text-amber-400" />
                <h3 className="text-lg font-semibold text-white">Key Symptoms</h3>
              </div>
              <p className="text-slate-300 text-sm leading-relaxed">{segmentData.symptoms}</p>
            </div>

            <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
              <div className="flex items-center gap-2 mb-4">
                <Shield className="w-5 h-5 text-red-400" />
                <h3 className="text-lg font-semibold text-white">Primary Risks</h3>
              </div>
              <p className="text-slate-300 text-sm leading-relaxed">{segmentData.risks}</p>
            </div>

            <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
              <div className="flex items-center gap-2 mb-4">
                <Lightbulb className="w-5 h-5 text-teal-400" />
                <h3 className="text-lg font-semibold text-white">Key Opportunities</h3>
              </div>
              <p className="text-slate-300 text-sm leading-relaxed">{segmentData.opportunities}</p>
            </div>
          </div>
        )}

        {/* Headline Message */}
        {segmentData && (
          <div className="bg-gradient-to-r from-teal-500/10 to-slate-800 rounded-xl p-6 border border-teal-500/30">
            <h3 className="text-xl font-bold text-white mb-2">{segmentData.headline_message}</h3>
            <p className="text-slate-300">{segmentData.summary_diagnosis}</p>
          </div>
        )}

        {/* Recommended Actions */}
        {actionsData.length > 0 && (
          <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
            <SectionHeader
              title="Recommended Actions"
              subtitle="Prioritized steps to advance your maturity"
            />
            <div className="space-y-4 mt-6">
              {actionsData.slice(0, 5).map((action, index) => {
                const ActionIcon = getActionIcon(action.action_type);
                return (
                  <div
                    key={action.id}
                    className="bg-slate-700/50 rounded-lg p-4 border border-slate-600"
                  >
                    <div className="flex items-start gap-4">
                      <div className="flex-shrink-0 w-8 h-8 bg-teal-500/20 rounded-full flex items-center justify-center">
                        <ActionIcon className="w-4 h-4 text-teal-400" />
                      </div>
                      <div className="flex-1">
                        <div className="flex items-center gap-2 mb-1">
                          <h4 className="font-semibold text-white">{action.action_title}</h4>
                          <span className="text-xs px-2 py-0.5 bg-slate-600 rounded-full text-slate-300">
                            {action.action_type}
                          </span>
                          <span className="text-xs px-2 py-0.5 bg-teal-500/20 rounded-full text-teal-400">
                            {action.time_horizon}
                          </span>
                        </div>
                        <p className="text-sm text-slate-400 mb-2">{action.action_rationale}</p>
                        <p className="text-sm text-teal-400">
                          <strong>Expected Outcome:</strong> {action.expected_outcome}
                        </p>
                      </div>
                      <div className="text-right">
                        <span className="text-2xl font-bold text-teal-400">#{index + 1}</span>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {/* Implementation Blockers */}
        {(assessmentData.blockers.length > 0 || assessmentData.otherBlockers) && (
          <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
            <SectionHeader
              title="Implementation Blockers"
              subtitle="Challenges identified that may impact implementation"
            />
            <div className="mt-4 space-y-2">
              {assessmentData.blockers.map((blocker, index) => (
                <div key={index} className="flex items-center gap-2 text-slate-300">
                  <div className="w-2 h-2 bg-amber-400 rounded-full" />
                  <span>{blocker}</span>
                </div>
              ))}
              {assessmentData.otherBlockers && (
                <div className="mt-4 p-3 bg-slate-700/50 rounded-lg">
                  <p className="text-sm text-slate-400 mb-1">Additional Notes:</p>
                  <p className="text-slate-300">{assessmentData.otherBlockers}</p>
                </div>
              )}
            </div>
          </div>
        )}

        {/* Detailed Maturity Responses */}
        <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
          <SectionHeader
            title="Detailed Maturity Responses"
            subtitle="View individual question responses and selected assessment choices"
          />

          <button
            onClick={() => setShowDetailedResponses(!showDetailedResponses)}
            className="btn btn-outline mt-4 mx-auto block"
          >
            {showDetailedResponses ? 'Hide detailed maturity responses' : 'Show detailed maturity responses'}
          </button>

          {showDetailedResponses && (
            <div className="mt-6 space-y-8">
              {/* Operational Excellence Section */}
              <div>
                <h4 className="text-xl font-bold text-teal-400 mb-4">Operational Excellence</h4>
                <div className="space-y-4">
                  {maturityQuestions.slice(0, 6).map((question, index) => {
                    const answer = assessmentData.answers[index] || 0;
                    const selectedOption = question.options[answer - 1] || '';
                    return (
                      <div key={index} className="bg-slate-700/50 rounded-lg p-4 border-l-4 border-teal-500">
                        <div className="flex items-start justify-between gap-4">
                          <div className="flex-1">
                            <h5 className="font-semibold text-white mb-2">{question.title}</h5>
                            <p className="text-sm text-slate-400 leading-relaxed">
                              {selectedOption}
                            </p>
                          </div>
                          <div className="text-right flex-shrink-0">
                            <div className="text-lg font-bold text-white">{answer} / 5</div>
                            <div className="w-20 h-2 bg-slate-600 rounded-full mt-1">
                              <div
                                className="h-2 bg-teal-500 rounded-full transition-all"
                                style={{ width: `${(answer / 5) * 100}%` }}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>

              {/* Strategic Maturity Section */}
              <div>
                <h4 className="text-xl font-bold text-teal-400 mb-4">Strategic Maturity</h4>
                <div className="space-y-4">
                  {maturityQuestions.slice(6, 11).map((question, index) => {
                    const actualIndex = index + 6;
                    const answer = assessmentData.answers[actualIndex] || 0;
                    const selectedOption = question.options[answer - 1] || '';
                    return (
                      <div key={actualIndex} className="bg-slate-700/50 rounded-lg p-4 border-l-4 border-teal-500">
                        <div className="flex items-start justify-between gap-4">
                          <div className="flex-1">
                            <h5 className="font-semibold text-white mb-2">{question.title}</h5>
                            <p className="text-sm text-slate-400 leading-relaxed">
                              {selectedOption}
                            </p>
                          </div>
                          <div className="text-right flex-shrink-0">
                            <div className="text-lg font-bold text-white">{answer} / 5</div>
                            <div className="w-20 h-2 bg-slate-600 rounded-full mt-1">
                              <div
                                className="h-2 bg-teal-500 rounded-full transition-all"
                                style={{ width: `${(answer / 5) * 100}%` }}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          )}
        </div>

        {/* Maturity Level Reference */}
        <div className="bg-slate-800 rounded-xl p-4 md:p-6 border border-slate-700 shadow-lg">
          <SectionHeader
            title="Maturity Level Reference"
            subtitle="Understanding the five stages of R2R maturity"
          />
          <div className="grid grid-cols-2 sm:grid-cols-5 gap-4 mt-6">
            {[
              { level: 'Manual', icon: User, color: 'text-red-400', bg: 'bg-red-500/20', description: 'Basic processes with limited automation' },
              { level: 'Siloed', icon: Building2, color: 'text-orange-400', bg: 'bg-orange-500/20', description: 'Departmental tools with minimal integration' },
              { level: 'Managed', icon: Settings, color: 'text-yellow-400', bg: 'bg-yellow-500/20', description: 'Coordinated processes with selective automation' },
              { level: 'Controlled', icon: Target, color: 'text-blue-400', bg: 'bg-blue-500/20', description: 'Orchestrated workflow with real-time visibility' },
              { level: 'Autonomous', icon: Bot, color: 'text-green-400', bg: 'bg-green-500/20', description: 'Fully automated with predictive capabilities' }
            ].map(({ level, icon: Icon, color, bg, description }, index) => {
              const isCurrentLevel = level === operationalBand;
              return (
                <div
                  key={level}
                  className={`text-center p-4 rounded-lg transition-all ${
                    isCurrentLevel
                      ? 'bg-teal-500/20 border-2 border-teal-500'
                      : 'bg-slate-700/30 border border-slate-600'
                  }`}
                >
                  <div className={`${bg} rounded-full w-10 h-10 flex items-center justify-center mx-auto mb-2`}>
                    <Icon className={`w-5 h-5 ${color}`} />
                  </div>
                  <div className="text-xs text-slate-500 mb-1">Level {index + 1}</div>
                  <h4 className={`font-medium text-sm mb-1 ${isCurrentLevel ? 'text-teal-400' : color}`}>
                    {level}
                  </h4>
                  <p className="text-slate-500 text-xs">{description}</p>
                </div>
              );
            })}
          </div>
        </div>

        {/* Action Buttons - Hidden during PDF generation */}
        {!isGeneratingPdf && (
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
            <button
              onClick={handleDownloadPdf}
              className="btn btn-primary flex items-center gap-2 px-8"
            >
              <Download className="w-4 h-4" />
              Download PDF Report
            </button>
            <button
              onClick={() => {
                // Clear the current assessment from localStorage
                if (effectiveSessionId) {
                  localStorage.removeItem(`assessment_${effectiveSessionId}`);
                }
                setLocation('/assessment');
              }}
              className="btn btn-outline flex items-center gap-2"
            >
              <RefreshCw className="w-4 h-4" />
              Take New Assessment
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
