import { useState, useEffect } from 'react';
import { Route, Switch, useLocation } from 'wouter';
import InAppAssessment from './pages/InAppAssessment';
import MaturityResults from './pages/MaturityResults';

interface AppProps {
  initialSessionId?: string | null;
}

function App({ initialSessionId }: AppProps) {
  const [sessionId, setSessionId] = useState<string | null>(initialSessionId || null);
  const [, setLocation] = useLocation();

  // Effect to handle initial session setup
  useEffect(() => {
    if (initialSessionId) {
      setSessionId(initialSessionId);
    }
  }, [initialSessionId]);

  // Handler for when assessment starts and creates a new session
  const handleSessionCreated = (newSessionId: string) => {
    setSessionId(newSessionId);
    // Update URL with session ID for bookmarking/sharing
    const url = new URL(window.location.href);
    url.searchParams.set('session', newSessionId);
    window.history.replaceState({}, '', url.toString());
  };

  // Handler for when assessment completes
  const handleAssessmentComplete = (completedSessionId: string) => {
    setLocation(`/results?session=${completedSessionId}`);
  };

  return (
    <div className="assessment-container">
      <Switch>
        {/* Assessment flow page */}
        <Route path="/">
          <InAppAssessment
            sessionId={sessionId}
            onSessionCreated={handleSessionCreated}
            onComplete={handleAssessmentComplete}
          />
        </Route>

        <Route path="/assessment">
          <InAppAssessment
            sessionId={sessionId}
            onSessionCreated={handleSessionCreated}
            onComplete={handleAssessmentComplete}
          />
        </Route>

        {/* Results page */}
        <Route path="/results">
          <MaturityResults sessionId={sessionId} />
        </Route>

        {/* Fallback - redirect to assessment */}
        <Route>
          <InAppAssessment
            sessionId={sessionId}
            onSessionCreated={handleSessionCreated}
            onComplete={handleAssessmentComplete}
          />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
