/**
 * Hook for communicating with parent window when embedded in an iframe.
 *
 * Handles:
 * - Detecting iframe context
 * - Sending events to parent (ready, complete, resize)
 * - Auto-resizing iframe based on content height
 */

import { useEffect, useCallback, useRef } from 'react';
import type { CompletionData } from '@shared/types';

// Check if we're running inside an iframe
export function isEmbedded(): boolean {
  try {
    return window.self !== window.top;
  } catch {
    // If we can't access window.top, we're likely in a sandboxed iframe
    return true;
  }
}

// Check if embed mode is enabled via URL param
export function isEmbedMode(): boolean {
  const params = new URLSearchParams(window.location.search);
  return params.get('embed') === 'true' || isEmbedded();
}

export function useParentCommunication() {
  const resizeObserverRef = useRef<ResizeObserver | null>(null);
  const lastHeightRef = useRef<number>(0);
  const embedded = isEmbedMode();

  // Send message to parent window
  // Note: Using '*' as targetOrigin because the embed can be used on any domain.
  // This is acceptable here because:
  // 1. We only send non-sensitive assessment scores (no PII)
  // 2. The parent must have initiated the embed (so they control what receives messages)
  // 3. Using a specific origin would break cross-domain embedding functionality
  const sendToParent = useCallback((type: string, payload?: unknown) => {
    if (!embedded) return;

    try {
      window.parent.postMessage({
        type: `redwood:assessment:${type}`,
        payload
      }, '*');
    } catch (error) {
      console.warn('[Assessment] Failed to send message to parent:', error);
    }
  }, [embedded]);

  // Notify parent that assessment is ready
  const notifyReady = useCallback(() => {
    sendToParent('ready');
  }, [sendToParent]);

  // Notify parent of assessment completion
  const notifyComplete = useCallback((data: CompletionData) => {
    sendToParent('complete', data);
  }, [sendToParent]);

  // Request iframe resize
  const requestResize = useCallback((height: number) => {
    // Only send if height changed significantly (>10px)
    if (Math.abs(height - lastHeightRef.current) > 10) {
      lastHeightRef.current = height;
      sendToParent('resize', { height });
    }
  }, [sendToParent]);

  // Auto-resize based on document height
  const setupAutoResize = useCallback(() => {
    if (!embedded) return;

    // Clean up existing observer
    if (resizeObserverRef.current) {
      resizeObserverRef.current.disconnect();
    }

    const updateHeight = () => {
      const height = Math.max(
        document.body.scrollHeight,
        document.body.offsetHeight,
        document.documentElement.scrollHeight,
        document.documentElement.offsetHeight
      );
      requestResize(height);
    };

    // Use ResizeObserver for efficient resize detection
    resizeObserverRef.current = new ResizeObserver(() => {
      updateHeight();
    });

    resizeObserverRef.current.observe(document.body);

    // Also update on DOM changes
    const mutationObserver = new MutationObserver(() => {
      updateHeight();
    });

    mutationObserver.observe(document.body, {
      childList: true,
      subtree: true,
      attributes: true
    });

    // Initial height update
    setTimeout(updateHeight, 100);

    return () => {
      resizeObserverRef.current?.disconnect();
      mutationObserver.disconnect();
    };
  }, [embedded, requestResize]);

  // Set up auto-resize on mount
  useEffect(() => {
    const cleanup = setupAutoResize();
    return cleanup;
  }, [setupAutoResize]);

  // Notify ready on mount
  useEffect(() => {
    if (embedded) {
      // Small delay to ensure iframe is fully loaded
      setTimeout(notifyReady, 50);
    }
  }, [embedded, notifyReady]);

  return {
    isEmbedded: embedded,
    notifyReady,
    notifyComplete,
    requestResize
  };
}
